* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  flex-direction: column;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

.header {
  font-size: 20px;
}

.search {
  margin-bottom: 16px;
  position: relative;
}

.search__box {
  font: inherit;
  min-width: 320px;
  border: 1px solid gray;
  border-radius: 10px;
  outline: none;
  padding: 8px 8px 8px 35px;
}

.search__box:focus {
  border: 2px solid rgba(13, 166, 213, .886);
}

.search__box:focus ~ .search__icon {
  fill: rgba(13, 166, 213, .886);
  rotate: 360deg;
}

.search__icon {
  width: 20px;
  height: 20px;
  fill: gray;
  transform-origin: center;
  transition: rotate 1s ease-in-out;
  display: block;
  position: absolute;
  top: 11px;
  left: 10px;
}

.country-list {
  flex-direction: column;
  gap: 10px;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.country-list__item {
  cursor: pointer;
  align-items: center;
  display: flex;
  position: relative;
}

.country-list__flag {
  width: 50px;
  border: 1px solid gray;
  margin-right: 20px;
}

.county-list__description {
  font-weight: 500;
}

.country-info__heading {
  text-transform: capitalize;
  font-size: 16px;
  font-weight: 700;
}

.country-info__details {
  padding-left: 10px;
  font-size: 20px;
  font-weight: 500;
}

.country-info__heading--unfold {
  color: #212121;
  font-size: 12px;
}

.country-list__description--unfold {
  color: rgba(11, 5, 98, .486);
  font-size: 16px;
  font-weight: 700;
}

.fa-solid {
  color: gray;
  margin-left: 10px;
  font-size: 16px;
}

